import React, { Component } from 'react';
import { Table } from 'react-bootstrap';
import './styles.scss';

class CustomTable extends Component {
  renderTableHeader() {
    return this.props.columns.map((col, index) => {
      return <th key={`header_${col.key}`} >{col.name}</th>
    })
  }
  renderTableData() {
    const { data, columns } = this.props;
    const keys = columns.map(item => item.key)
    return data.map((row, index) => {
      return (
        <tr key={row.id}>
          {
            keys.map((key, keyIndex) => {
              if (columns[keyIndex].render && typeof (columns[keyIndex].render) === 'function') {
                return <td>{columns[keyIndex].render(row[key], row)}</td>
              }
              if (row[key] !== undefined) {
                return <td>{row[key]}</td>
              }
            })
          }
        </tr>
      )
    })
  }
  render() {
    const { header, onCreate } = this.props
    return (
      <div className="custom-table">
        <div className="header">{header}</div>
        {
          onCreate && <div ><button className="create-btn" onClick={onCreate}>Create</button></div>
        }
        <Table responsive="xl">
          <thead>
            <tr className="table-header">
              {
                this.renderTableHeader()
              }
            </tr>
          </thead>
          <tbody className="table-wrapper">
            {
              this.renderTableData()
            }

          </tbody>
        </Table>
      </div>
    )
  }
}

export default CustomTable;