import React, { Component } from 'react';
import StarRatings from 'react-star-ratings';
import './styles.scss';
import {CustomTable} from '../../components/table';
import {getHumans} from '../../services/humansService';
import {Level} from '../../components/level';
class Humans extends Component {
    constructor(props){
        super(props)
        this.state = {
            humans: [],
            page: 1,
            limit: 5,
        }
    }
    componentDidMount(){
        getHumans().then(res => this.setState({humans: res.data}))
        
    }
    handleEdit = (row) =>{
        alert(row.id)
    }
    handleDelete = (row) => {
        alert(row.id)
    }
    render() {
        const { humans } = this.state;
        const columns = [
            {
                key: 'id',
                name: '#'
            },
            {
                key: 'name',
                name: 'Name'
            },
            {
                key: 'point',
                name: 'Point',
                render: (point) => <span style={{color: point >=0 ? 'black' : 'red'}}>{point}</span>
            },
            {
                key: 'level',
                name: 'Level',
                render: (level)=> {
                    return <Level maxLevel={10} currentLevel={level}/>
                }
            },
            {
                key: 'star',
                name: 'Star',
                render: (star) => {
                    return (
                        <StarRatings
                        rating={star || 0}
                        starRatedColor="blue"
                        numberOfStars={5}
                        name='rating'
                        />
                    )
                }
            }
            ,
            {
                key: 'action',
                name: 'Action',
                render: (_,row) => {
                    return <div className="btn-actions">
                        <div>                        
                            <button className="btn btn-primary my-btn" onClick={()=>this.handleEdit(row)}><i class="fa fa-pen"></i></button>
                        </div>
                        <div>
                            <button className="btn btn-danger my-btn" onClick={()=>this.handleDelete(row)}><i class="fa fa-trash-alt"></i></button>
                        </div>
                    </div>
                }
            }

        ]
        return (
            <div style={{height: '100vh', padding: '100px 100px', background: 'white', display: 'felx', justifyContent: 'center', alignItems: 'center'}}>
                <CustomTable onCreate={()=> alert('add')} header={"Humans"} columns = {columns} data = {humans}/>
            </div>
        )
    }
}
export default Humans;